<template>
  <div>
    <div class="basic-info">
      <el-row>
        <el-col v-bind="colOptions" :span="8" v-for="(item, index) of configList" :key="index">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">{{ item.label }}</div>
            <div class="basic-info-card__value">{{ item.value }}</div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'BasicInfoTow',

  props: {
    basicInfoData: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      propsOrder: [
        {
          name: 'parentTaskId',
          label: this.$t('TAC.taskDetail.originalTask'),
        },
        {
          name: 'taskDesc',
          label: this.$t('TAC.taskDetail.taskDescription'),
        },
      ],
    };
  },

  computed: {
    pathQuery() {
      return `detail?taskId=8K1000063`;
    },
    configList() {
      const configMap = this.basicInfoData;
      return this.propsOrder.map((field) => {
        const name = field.name;
        const label = field.label;
        return {
          label,
          value: configMap[name] || '-',
        };
      });
    },
  },
};
</script>
