<template>
  <div class="workflow-detail-pending">
    <div class="basic-info">
      <div class="basic-info-title">
        <div class="title">
          <span>{{ title }} {{ $t('TAC.workflowDetail.detail') }}</span>
          <div class="processing" v-if="status === 'Expired'">{{ $t('TAC.taskDetail.processing') }}</div>
          <div class="pending" v-if="status === 'Pending'">{{ $t('TAC.workflowDetail.pending') }}</div>
          <div class="pause" v-if="status === 'Pause'">{{ $t('TAC.workflowDetail.pause') }}</div>
          <div class="completed" v-if="status === 'Completed'">{{ $t('TAC.workflowDetail.completed') }}</div>
        </div>
      </div>
      <el-divider class="table-divider"></el-divider>
      <div class="content">
        <basic-info-item :basicInfo="basicInfoList"></basic-info-item>
      </div>
    </div>
    <div class="basic-info">
      <div class="specific-info-title">{{ $t('TAC.workflowDetail.specificInfo') }}</div>
      <el-divider class="table-divider"></el-divider>
      <div class="specific-content">
        <div class="specific-content-title">{{ $t('TAC.workflowDetail.basicInfo') }}</div>
        <basic-info-tow :basicInfoData="specificDetailData" v-if="!specificInfoDetail"></basic-info-tow>
        <basic-info-box :basicInfoItem="specificBasicInfoList" v-if="specificInfoDetail"></basic-info-box>
        <el-divider class="table-divider" v-if="specificInfoDetail"></el-divider>
        <div class="specific-content-title" v-if="specificInfoDetail">{{ $t('TAC.workflowDetail.changeInfo') }}</div>
        <basic-info-item-box
          :basicInfoItemList="specificChangeInfoList"
          v-if="specificInfoDetail"
        ></basic-info-item-box>
      </div>
    </div>
    <div class="basic-info">
      <div class="history-process-title">{{ $t('TAC.workflowDetail.historyProcessRecord') }}</div>
      <el-divider class="table-divider"></el-divider>
      <div>
        <div class="history-process-record">
          <el-collapse class="inner-text">
            <el-collapse-item class="history-process-record-item">
              <template slot="title">
                <span class="item-text">{{ detailDatas.taskId }} - {{ taskName }}</span>
              </template>
              <div v-for="items in recordCommonList" :key="items.commentId">
                <el-collapse class="inner-text" v-if="items.commentText">
                  <el-collapse-item class="history-process-record-item">
                    <template slot="title">
                      <span class="item-text">{{ items.creatorId }} - {{ items.creatorName }}</span>
                      <span class="content-time">{{ items.action }}</span>
                      <span class="content-time">{{ items.createTime }}</span>
                    </template>
                    <div class="item-content">
                      <span class="content-detail">{{ items.commentText }}</span>
                    </div>
                  </el-collapse-item></el-collapse
                >
              </div>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { dateFormat } from '@/common/utils/date-time-utils';
import { queryTaskDetail, queryOrgOperatorDetail } from '@/views/task-center/api/api-task-center';
import BasicInfoItem from './components/basic-info-item.vue';
import BasicInfoBox from './components/basic-info-box.vue';
import BasicInfoItemBox from './components/basic-info-item-box.vue';
import BasicInfoTow from './components/basic-info-tow.vue';
import { STATUS_MAP, EVENT_SOURCE_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';

export default {
  components: { BasicInfoItem, BasicInfoBox, BasicInfoItemBox, BasicInfoTow },
  data() {
    return {
      channel: '',
      specificInfoDetail: false,
      recordCommonList: [],
      taskName: null,
      title: '',
      basicInfoList: {},
      specificDetailData: {},
      specificBasicInfoList: {},
      specificChangeInfoList: {},
    };
  },
  computed: {
    workflowId() {
      return this.$route.query.taskId;
    },
    detailDatas() {
      return this.$route.query;
    },
    status() {
      return this.$route.query.status;
    },
  },
  created() {
    this.getDetailData();
  },
  methods: {
    showChannel(val) {
      switch (val) {
        case '1005':
          return 'Web';
        case '1007':
          return 'Handset';
        case '1010':
          return 'API';
        default:
          return;
      }
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
    async getDetailData() {
      let params = {
        taskId: this.workflowId,
      };
      let res = await queryTaskDetail(params);
      if (!res) {
        this.basicInfoList = {};
        return;
      }
      this.recordCommonList = res.comments ? res.comments : [];
      this.title = res.taskDTO.taskId;
      this.taskName = res.taskDTO.taskName || '';
      this.basicInfoList = { ...res.taskDTO, ...res.processDTO };

      this.basicInfoList.eventSource = res.bizContext ? EVENT_SOURCE_MAP[res.bizContext.eventSource] : '';
      const jsonStr = res.bizContext && res.bizContext.variables && res.bizContext.variables.changeInfo;

      if (jsonStr) {
        this.specificInfoDetail = true;
        this.channel = res.bizContext.channel;
        let newChannel = '';
        if (this.channel === '1005' || this.channel === '1007' || this.channel === '1010') {
          newChannel = this.showChannel(this.channel);
        } else {
          newChannel = this.channel;
        }

        this.basicInfoList.initiationChannel = newChannel;
        let changeInfoObj = JSON.parse(jsonStr);
        const param = `receiverIdentityId=${changeInfoObj.receiverIdentityId}`;
        let orgResList = await queryOrgOperatorDetail(param);
        const orgOperatorInfo = orgResList.orgOperatorInfo;
        orgOperatorInfo.accessChannel.forEach((item, idx) => {
          switch (item) {
            case '1005':
              orgResList.orgOperatorInfo.accessChannel[idx] = 'Web';
              break;
            case '1007':
              orgResList.orgOperatorInfo.accessChannel[idx] = 'Handset';
              break;
            case '1010':
              orgResList.orgOperatorInfo.accessChannel[idx] = 'API';
              break;
            default:
              return;
          }
        });
        this.specificBasicInfoList = orgOperatorInfo;
        this.specificBasicInfoList.status = this.showStatus(orgOperatorInfo.status);
        this.specificBasicInfoList.accessChannel = orgOperatorInfo.accessChannel.join();
        let createTime = orgResList.orgOperatorInfo.createTime.map((item) => {
          if (item < 10) {
            return (item = '0' + item);
          } else {
            return item;
          }
        });
        let curTime = createTime.join().replace(/,/g, '');
        this.specificBasicInfoList.createTime = dateFormat(curTime, 'DD-MM-YYYY HH:mm:ss', 'YYYYMMDDHHmmss');
        this.specificChangeInfoList = {
          oldStatus: this.showStatus(changeInfoObj.oldStatus),
          newStatus: this.showStatus(changeInfoObj.newStatus),
          reason: changeInfoObj.remark,
          remark: changeInfoObj.remark,
        };
      } else {
        this.specificInfoDetail = false;
        this.specificDetailData = { ...res.taskDTO };
      }
    },
  },
};
</script>

