<template>
  <div>
    <div class="basic-info-box">
      <el-row>
        <el-col v-bind="colOptions" :span="8" v-for="(item, index) of configList" :key="index">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">{{ item.label }}</div>
            <div class="basic-info-card__value">{{ item.value }}</div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'BasicInfoBox',

  props: {
    basicInfo: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      propsOrder: [
        {
          name: 'processId',
          label: this.$t('TAC.workflowDetail.workflowId'),
        },
        {
          name: 'createTime',
          label: this.$t('TAC.workflowDetail.creationTime'),
        },
        {
          name: 'createTime',
          label: this.$t('TAC.workflowDetail.startTime'),
        },
        {
          name: 'initiatorId',
          label: this.$t('TAC.workflowDetail.initiator'),
        },
        {
          name: 'initiationChannel',
          label: this.$t('TAC.workflowDetail.initiationChannel'),
        },
        {
          name: 'eventSource',
          label: this.$t('TAC.workflowDetail.initiationResource'),
        },
        {
          name: 'initiationResourceSystem',
          label: this.$t('TAC.workflowDetail.initiationResourceSystem'),
        },
        {
          name: 'receiver',
          label: this.$t('TAC.workflowDetail.receiver'),
        },
        {
          name: 'taskState',
          label: this.$t('TAC.workflowDetail.taskStatus'),
        },
      ],
    };
  },

  computed: {
    configList() {
      const configMap = this.basicInfo;
      return this.propsOrder.map((field) => {
        const name = field.name;
        const label = field.label;
        return {
          label,
          value: configMap[name] || '-',
        };
      });
    },
  },
};
</script>
<style scoped lang="scss">
.basic-info {
  font-weight: bold;
  font-size: 16px;
}
.el-row {
  .basic-info-card {
    padding-bottom: 20px;
    font-size: 14px;

    &__label {
      margin-bottom: 8px;
      color: $--text_color--secondary;
    }

    &__value {
      color: $--text_color--strong;
    }
  }
}
</style>
